/* BoxComponent.css */
.box-container {
  min-width: 550px;
  width: 35%;
  height: calc(100vh - 140px);
  background-color: #efefef;
  border: 1px solid #A1AAB5;
  border-radius: 5px;
  padding: 10px;
  transition: width 0.5s, height 0.5s;
  display: flex;
  flex-direction: column;
  z-index: 100;
  position: absolute;
  top: 0;
  margin-top: 100px;
}

.box-container.expanded {
  width: calc(100vw - 60px);
  height: calc(100vh - 140px);
  /* min-width: 1200px; */
}

@media screen and (min-width: 1480px) {
  .box-container {
    width: 550px;
    min-width: 480px;
  }
}


.box-header {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 5px;
  margin-right: 10px;
}

.toggle-icon {
  cursor: pointer;
  color: #68727D;
  transform: scale(1.5);
}

.box-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}