.toggle-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 6px;
}
  
  .toggle {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 14px;
    background-color: #68727D;
    border-radius: 5px;
    cursor: pointer;
}

.slider {
    position: absolute;
    top: 2px;
    left: 2px;
    width: 10px;
    height: 10px;
    background-color: #FFFFFF;
    border-radius: 50%;
    transition: all 0.3s ease;
}

.toggle.on {
    background-color: #0A77FF;
}

.toggle.off .slider {
    transform: translateX(0);
}

.toggle.on .slider {
    transform: translateX(16px);
}

.label {
    font-size: 10px;
    font-weight: 500;
    color: #68727D;
    /* dont wrap text */
    white-space: nowrap;
}