/* Searchbar.css */

.searchbar-container {
    background-color: #fff;
    border-radius: 5px;
    border: 1.5px solid #C8D0D6;
    /* width: 40%; */
    /* min-width: 220px; */
    width: 270px;
    display: flex;
    flex-direction: row;
    padding: 0 10px;
    height: 30px;
    display: flex;
    align-items: center;
    transition: width 0.5s;
    z-index: 1000;
}

.searchbar-input {
    width: 100%;
    border: none;
    background: none;
    color: #68727D;
    font-size: 14px;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    /* align content center horizontally */
    display: flex;
    align-items: center;
    /* remove default input focus */
    outline: none;
}

/* class to edit on input click */
.searchbar-input:focus {
    outline: none;
}

.searchbar-icon {
    color: #68727D;
    font-size: 24px;
    cursor: pointer;
    margin-right: 5px;
    flex-shrink: 0;
}