/* AtmosphericDataIcon.css */
.atmospheric-data-icon {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin-right: 40px;
}

.title-row {
    display: flex;
    justify-content: flex-start;
}

.title {
    font-size: 10px;
    font-weight: 500;
    color: #68727D;
    width: max-content;
    display: flex;
    align-items: flex-start;
}

.icon-value-row {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-top: 2px;
    margin-bottom: 10px;
    padding-left: 2px;
    width:fit-content;
}

.icon-column {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #0A77FF;
    transform: scale(1.5);
    margin-left: 10px;
    flex-shrink: 0;
}

.value-column {
    display: flex;
    padding-left: 4px;
}

.value {
    font-size: 10px;
    font-weight: 600;
    color: #2B2B2B;
    display: flex;
}

.wind-direction-icon {
    color: #2B2B2B;
    right: -2px;
    padding-top: 2px;
    position: relative;
}
