/* SearchbarDrowpdown.css */
.dropdown {
    position: absolute;
    top: 95px;
    left: 30px;
    width: 270px;
    background-color: white;
    border: 1px solid #C8D0D6;
    border-radius: 5px;
    z-index: 1000;
    /* align items horizontally */
    display: flex;
    flex-direction: column;
    align-items: center;

}

.dropdown-item {
    padding: 8px 16px;
    cursor: pointer;
    transition: background-color 0.3s;
    color: #68727D;
    font-size: 14px;
    width: 100%;
    
}

.dropdown-item:hover {
    font-weight: 500;
}