/* WeatherBox.css */
.weather-box-container {
    display: flex;
    flex-direction: column;
    padding: 0 10px;
}

@media screen and (min-height: 768px) {
    .weather-box-container {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
}

.forecast-box-text {
    display: flex;
    align-items: center;
    flex-direction: row;
    font-size: 14px;
    font-weight: 400;
    color: #68727D;
    align-self: flex-start;
}

.forecast-content-expanded {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: auto;
    margin: 0 auto;
}

.forecast-content-collapsed {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}