/* WeatherBox.css */
.info-box-container-table {
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    margin-top: -20px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.info-box-container-graph {
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    margin-top: -20px;
    overflow-y: hidden;
    overflow-x: hidden;
}

.info-header {
    display: flex;
    align-items: center;
}

.info-content-table {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-color: #C8D0D6 #F5F5F5;
    scrollbar-width: thin;
    scroll-behavior: smooth;
    height: 100%;
}

.info-content-graph {
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
    overflow-x: hidden;
    width: 100%;
}

.box-text {
    font-size: 24px;
    font-weight: bold;
    color: #68727D;
    margin: 0;
}

.info-ellipse-container {
    width: 20px;
    height: 20px;
    margin-left: 2px;
    /* margin-left: 6px; with ellipse bored */
}

/* .info-box-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
} */

.info-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.info-icon img {
    width: 80px;
    height: 80px;
    margin-right: 20px;
}

.temperature {
    display: flex;
    align-items: flex-start;
}

.temperature-text {
    font-size: 52px;
    font-weight: bold;
    color: #2B2B2B;
}

.temperature-unit {
    font-size: 22px;
    font-weight: bold;
    color: #2B2B2B;
    margin-top: 10px;
}

.expanded-atmospheric-data {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-left: 40px;
    width: 50%;
}

@media screen and (min-width: 1480px) {
    .expanded-atmospheric-data {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin-left: 40px;
        width: 32%;
        justify-content: space-evenly;
    }

    .collapsed-atmospheric-data {
        display: flex;
        flex-direction: row;
        margin-left: 10px;
        width: 60%;
        justify-content: space-evenly;
    }

    .info-icon img {
        width: 90px;
        height: 90px;
        margin-right: 20px;
    }
}

.collapsed-atmospheric-data {
    display: flex;
    flex-direction: row;
    margin-left: 20px;
    width: 60%;
    justify-content: space-evenly;
}

.column {
    display: flex;
    flex-direction: column;
}

.info-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-self: center;
    width: 600px;
    margin-top: 20px;
}