/* WeatherComponent.css */
.weather-component {
  position: relative;
  width: 60px;
  height: 54px;
}

.weather-icon {
  /* align center horizontally */
  justify-content: center;
  /* move to the right */
  margin-left: 12px;
  transform: scale(1.06);
  cursor: pointer;
}

.weather-details {
  position: absolute;
  top: 26%;
  right: 0;
  transform: translateY(-50%);
  width: 15px;
  height: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.weather-detail-top,
.weather-detail-bottom {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 10px;
  font-weight: 600;
}

.ellipse-container {
  position: absolute;
  width: 12px;
  height: 12px;
}

.weather-text {
  position: absolute;
  top: 56%;
  right: 0px;
  font-size: 11px;
  font-weight: 500;
  color: #303236;
  line-height: 1;
  text-align: right;
  width: fit-content;
}

.wave-icon.bottom {
  position: absolute;
  bottom: 0;
  height: 40%;
}

.wave-icon.left {
  position: absolute;
  bottom: 0;
  height: 90%;
  left: -20px;
}

.wave-icon.right {
  position: absolute;
  right: -26px;
  top: 6px;
}
