/* GraphBox.css */
.graph-box-container {
    display: flex;
    flex-direction: column;
}

.toggle-box-container-expanded {
    padding: 20px;
    margin-top: 10px;
    width: 100%;
    height: 80px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-self: center;
}

.toggle-box-container-collapsed {
    padding: 20px;
    margin-top: 10px;
    width: 100%;
    height: 130px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-self: center;
}

.chart-container {
    width: 100%;
    scrollbar-color: #C8D0D6 #F5F5F5;
    scrollbar-width: thin;
    scroll-behavior: smooth;
    overflow-y: hidden;
    overflow-x: scroll;
}

.toggles-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
}

.toggle-grid-1 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 8px;
    column-gap: 20px;
}

.toggle-grid-1-expanded {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 18px;
}


.toggle-grid-2 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 8px;
    column-gap: 20px;
}

.toggle-grid-2-expanded {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    row-gap: 8px;
    column-gap: 20px;
    width: 100%;
}

.toggle-grid-3 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 40px;
}

.toggle-navigation {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    width: 10%;
}

.navigation-arrow {
    cursor: pointer;
    font-size:30px;
    color: #68727D;
}

.level-3-text {
    color: #68727D;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 10px;
}

.toggle-grid {
    /* add sliding animation */
    transition: all 0.5s ease;
}


.toggle-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 18px;
}

.chart-box-text {
    display: flex;
    align-items: center;
    flex-direction: row;
    font-size: 14px;
    font-weight: 400;
    color: #68727D;
    align-self: center;
}